// components/SimpleHero.js
import Link from 'next/link';

export default function SimpleHero({ 
  title = "Premium Pflegekräfte für Ihre individuellen Bedürfnisse",
  subtitle = "Professionelle Vermittlung von qualifizierten Pflegekräften",
  buttonText = "KONTAKT AUFNEHMEN",
  buttonLink = "/kontakt"
}) {
  return (
    <section className="bg-black-custom py-5">
      <div className="container">
        <div className="row align-items-center">
          <div className="col-lg-6 mb-5 mb-lg-0">
            <h1 className="display-4 fw-bold mb-4">{title}</h1>
            <p className="lead mb-5">{subtitle}</p>
            <Link href={buttonLink} className="btn btn-gold px-4 py-2">
              {buttonText}
            </Link>
          </div>
          
          <div className="col-lg-6">
            <div className="bg-dark-gray p-5 text-center rounded" style={{minHeight: '300px'}}>
              <p className="text-muted">Bild: Professionelle Pflege</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}